import { useState, useEffect } from "react";
import { ArrowLeft, RefreshCw, Package, Users, CheckCircle, Clock, TrendingUp, BarChart3 } from "lucide-react";

const API_URL = "http://localhost:8080/api";

type BuyerOrderHistory = {
  id: number;
  buyer_order_id: number;
  buyer_user_id: number;
  buyer_transaction_id: string;
  original_price: number;
  original_qty: number;
  buyer_trade_date: string;
  buyer_trade_time: string;
  project_id: number;
  total_matched_qty: number;
  remaining_qty: number;
  match_count: number;
  seller_count: number;
  status: string;
  created_at: string;
  updated_at: string;
};

type MatchAssignment = {
  id: number;
  buyer_order_id: number;
  seller_order_id: number;
  seller_user_id: number;
  seller_transaction_id: string;
  seller_total_qty: number;
  assigned_qty: number;
  seller_price: number;
  matched_order_id: number;
  assigned_at: string;
};

export default function BuyerOrderDetailsPage() {
  const [buyerHistory, setBuyerHistory] = useState<BuyerOrderHistory | null>(null);
  const [assignments, setAssignments] = useState<MatchAssignment[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [buyerId, setBuyerId] = useState<number | null>(null);

  useEffect(() => {
    const params = new URLSearchParams(window.location.search);
    const id = params.get("buyer_id");
    if (id) {
      setBuyerId(parseInt(id));
    }
  }, []);

  const fetchData = async () => {
    if (!buyerId) return;
    setLoading(true);
    setError(null);

    try {
      const historyResponse = await fetch(`${API_URL}/buyer-history/${buyerId}`);
      if (!historyResponse.ok) throw new Error("Failed to fetch buyer order history");
      const historyData = await historyResponse.json();
      setBuyerHistory(historyData);

      const assignmentsResponse = await fetch(`${API_URL}/match-assignments/${buyerId}`);
      if (!assignmentsResponse.ok) throw new Error("Failed to fetch match assignments");
      const assignmentsData = await assignmentsResponse.json();
      setAssignments(assignmentsData || []);
    } catch (err) {
      setError(err instanceof Error ? err.message : "Unknown error occurred");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (buyerId) fetchData();
  }, [buyerId]);

  const formatDate = (dateStr: string) => {
    try {
      const date = new Date(dateStr);
      return date.toLocaleDateString('en-US', { year: 'numeric', month: 'short', day: 'numeric' });
    } catch {
      return dateStr;
    }
  };

  const formatDateTime = (dateStr: string) => {
    try {
      const date = new Date(dateStr);
      return date.toLocaleString('en-US', {
        year: 'numeric',
        month: 'short',
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    } catch {
      return dateStr;
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'Completed':
        return 'bg-emerald-100 text-emerald-800 border-emerald-300';
      case 'Partially Matched':
        return 'bg-amber-100 text-amber-800 border-amber-300';
      case 'Pending':
        return 'bg-slate-100 text-slate-800 border-slate-300';
      default:
        return 'bg-slate-100 text-slate-800 border-slate-300';
    }
  };

  const calculatePercentage = (matched: number, total: number) => {
    if (total === 0) return 0;
    return ((matched / total) * 100).toFixed(1);
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-slate-50 to-slate-100 py-8 px-4">
        <div className="max-w-7xl mx-auto">
          <div className="flex justify-center items-center py-20">
            <div className="text-center">
              <RefreshCw className="animate-spin mx-auto mb-4 text-blue-600" size={48} />
              <p className="text-slate-600 font-medium">Loading order details...</p>
            </div>
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-slate-50 to-slate-100 py-8 px-4">
        <div className="max-w-7xl mx-auto">
          <button
            onClick={() => window.history.back()}
            className="mb-6 flex items-center gap-2 px-4 py-2 bg-white border border-slate-300 rounded-lg hover:bg-slate-50 transition-all shadow-sm"
          >
            <ArrowLeft size={20} />
            <span className="font-medium">Back</span>
          </button>
          <div className="bg-red-50 border border-red-200 p-8 text-center rounded-lg">
            <p className="text-red-600 font-semibold">{error}</p>
          </div>
        </div>
      </div>
    );
  }

  if (!buyerHistory) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-slate-50 to-slate-100 py-8 px-4">
        <div className="max-w-7xl mx-auto">
          <button
            onClick={() => window.history.back()}
            className="mb-6 flex items-center gap-2 px-4 py-2 bg-white border border-slate-300 rounded-lg hover:bg-slate-50 transition-all shadow-sm"
          >
            <ArrowLeft size={20} />
            <span className="font-medium">Back</span>
          </button>
          <div className="bg-amber-50 border border-amber-200 p-8 text-center rounded-lg">
            <p className="text-amber-600 font-semibold">No order data found</p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-slate-100 py-8 px-4">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="mb-8 flex items-center justify-between">
          <button
            onClick={() => window.history.back()}
            className="flex items-center gap-2 px-4 py-2 bg-white border border-slate-300 rounded-lg hover:bg-slate-50 transition-all shadow-sm"
          >
            <ArrowLeft size={20} />
            <span className="font-medium">Back</span>
          </button>
          <button
            onClick={fetchData}
            disabled={loading}
            className="flex items-center gap-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-all disabled:opacity-50 shadow-sm"
          >
            <RefreshCw size={20} className={loading ? "animate-spin" : ""} />
            <span className="font-medium">Refresh</span>
          </button>
        </div>

        {/* Main Content */}
        <div className="space-y-6">
          {/* Order Summary Card */}
          <div className="bg-white rounded-lg shadow-md border border-slate-200 p-6">
            <div className="flex items-center gap-3 mb-6">
              <div className="p-3 bg-blue-100 rounded-lg">
                <Package className="text-blue-600" size={28} />
              </div>
              <div>
                <h1 className="text-2xl font-bold text-slate-900">Buyer Order #{buyerHistory.buyer_order_id}</h1>
                <p className="text-sm text-slate-600">Transaction ID: {buyerHistory.buyer_transaction_id}</p>
              </div>
            </div>

            <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
              <div className="bg-slate-50 p-4 rounded-lg border border-slate-200">
                <p className="text-xs text-slate-600 font-semibold mb-1">BUYER USER ID</p>
                <p className="text-xl font-bold text-slate-900">{buyerHistory.buyer_user_id}</p>
              </div>
              <div className="bg-slate-50 p-4 rounded-lg border border-slate-200">
                <p className="text-xs text-slate-600 font-semibold mb-1">PROJECT ID</p>
                <p className="text-xl font-bold text-slate-900">{buyerHistory.project_id}</p>
              </div>
              <div className="bg-slate-50 p-4 rounded-lg border border-slate-200">
                <p className="text-xs text-slate-600 font-semibold mb-1">TRADE DATE</p>
                <p className="text-lg font-bold text-slate-900">{formatDate(buyerHistory.buyer_trade_date)}</p>
                <p className="text-xs text-slate-500 mt-1">{buyerHistory.buyer_trade_time}</p>
              </div>
              <div className="bg-slate-50 p-4 rounded-lg border border-slate-200">
                <p className="text-xs text-slate-600 font-semibold mb-1">STATUS</p>
                <span className={`inline-block px-3 py-1 rounded-full text-xs font-bold border ${getStatusColor(buyerHistory.status)}`}>
                  {buyerHistory.status}
                </span>
              </div>
            </div>
          </div>

          {/* Order Details */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <div className="bg-white rounded-lg shadow-md border border-slate-200 p-6">
              <div className="flex items-center justify-between mb-4">
                <h3 className="font-semibold text-slate-900">Original Order</h3>
                <Package size={20} className="text-slate-400" />
              </div>
              <div className="space-y-3">
                <div>
                  <p className="text-xs text-slate-600 font-semibold">Price</p>
                  <p className="text-2xl font-bold text-slate-900">${buyerHistory.original_price.toFixed(2)}</p>
                </div>
                <div>
                  <p className="text-xs text-slate-600 font-semibold">Quantity</p>
                  <p className="text-2xl font-bold text-slate-900">{buyerHistory.original_qty} units</p>
                </div>
              </div>
            </div>

            <div className="bg-white rounded-lg shadow-md border border-slate-200 p-6">
              <div className="flex items-center justify-between mb-4">
                <h3 className="font-semibold text-slate-900">Matched Progress</h3>
                <TrendingUp size={20} className="text-emerald-500" />
              </div>
              <div className="space-y-3">
                <div>
                  <p className="text-xs text-slate-600 font-semibold">Total Matched</p>
                  <p className="text-2xl font-bold text-emerald-600">{buyerHistory.total_matched_qty}</p>
                </div>
                <div>
                  <p className="text-xs text-slate-600 font-semibold">Progress</p>
                  <p className="text-lg font-bold text-slate-900">{calculatePercentage(buyerHistory.total_matched_qty, buyerHistory.original_qty)}%</p>
                </div>
              </div>
            </div>

            <div className="bg-white rounded-lg shadow-md border border-slate-200 p-6">
              <div className="flex items-center justify-between mb-4">
                <h3 className="font-semibold text-slate-900">Match Details</h3>
                <BarChart3 size={20} className="text-purple-500" />
              </div>
              <div className="space-y-3">
                <div>
                  <p className="text-xs text-slate-600 font-semibold">Match Count</p>
                  <p className="text-2xl font-bold text-slate-900">{buyerHistory.match_count}</p>
                </div>
                <div>
                  <p className="text-xs text-slate-600 font-semibold">Sellers Involved</p>
                  <p className="text-2xl font-bold text-slate-900">{buyerHistory.seller_count}</p>
                </div>
              </div>
            </div>
          </div>

          {/* Progress Bar */}
          <div className="bg-white rounded-lg shadow-md border border-slate-200 p-6">
            <div className="flex justify-between items-center mb-3">
              <h3 className="font-semibold text-slate-900">Fulfillment Progress</h3>
              <span className="text-sm font-bold text-slate-600">{calculatePercentage(buyerHistory.total_matched_qty, buyerHistory.original_qty)}%</span>
            </div>
            <div className="w-full bg-slate-200 rounded-full h-4 overflow-hidden">
              <div
                className="bg-gradient-to-r from-emerald-500 to-emerald-600 h-4 rounded-full transition-all duration-500"
                style={{ width: `${calculatePercentage(buyerHistory.total_matched_qty, buyerHistory.original_qty)}%` }}
              />
            </div>
            <div className="flex justify-between text-xs text-slate-600 mt-3">
              <span>Matched: {buyerHistory.total_matched_qty} units</span>
              <span>Remaining: {buyerHistory.remaining_qty} units</span>
            </div>
          </div>

          {/* Seller Assignments Table */}
          <div className="bg-white rounded-lg shadow-md border border-slate-200 p-6">
            <div className="flex items-center justify-between mb-6">
              <div className="flex items-center gap-3">
                <div className="p-2 bg-purple-100 rounded-lg">
                  <Users className="text-purple-600" size={24} />
                </div>
                <h2 className="text-xl font-bold text-slate-900">Seller Assignments</h2>
              </div>
              <span className="px-3 py-1 bg-purple-100 text-purple-800 rounded-full text-sm font-semibold">
                {assignments.length} seller(s)
              </span>
            </div>

            {assignments.length === 0 ? (
              <div className="text-center py-12 bg-slate-50 rounded-lg border border-slate-200">
                <Clock className="mx-auto mb-3 text-slate-400" size={48} />
                <p className="text-slate-600 font-medium">No seller assignments yet</p>
              </div>
            ) : (
              <div className="overflow-x-auto">
                <table className="w-full">
                  <thead className="bg-slate-50 border-b border-slate-200">
                    <tr>
                      <th className="px-4 py-3 text-left text-xs font-semibold text-slate-700">Seller Order</th>
                      <th className="px-4 py-3 text-left text-xs font-semibold text-slate-700">Transaction ID</th>
                      <th className="px-4 py-3 text-left text-xs font-semibold text-slate-700">Price</th>
                      <th className="px-4 py-3 text-left text-xs font-semibold text-slate-700">Total Qty</th>
                      <th className="px-4 py-3 text-center text-xs font-semibold text-slate-700 bg-blue-50">Assigned Qty</th>
                      <th className="px-4 py-3 text-left text-xs font-semibold text-slate-700">Match ID</th>
                      <th className="px-4 py-3 text-left text-xs font-semibold text-slate-700">Assigned At</th>
                    </tr>
                  </thead>
                  <tbody>
                    {assignments.map((assignment, idx) => (
                      <tr key={assignment.id} className={idx % 2 === 0 ? "bg-white" : "bg-slate-50"}>
                        <td className="px-4 py-3 text-sm font-semibold text-slate-900">#{assignment.seller_order_id}</td>
                        <td className="px-4 py-3 text-sm text-slate-700 font-mono">{assignment.seller_transaction_id}</td>
                        <td className="px-4 py-3 text-sm font-semibold text-slate-900">${assignment.seller_price.toFixed(2)}</td>
                        <td className="px-4 py-3 text-sm text-slate-700">
                          <span className="px-2 py-1 bg-slate-100 rounded text-xs font-medium">
                            {assignment.seller_total_qty}
                          </span>
                        </td>
                        <td className="px-4 py-3 text-center bg-blue-50">
                          <span className="px-3 py-1 bg-blue-600 text-white rounded-full font-bold text-sm">
                            {assignment.assigned_qty}
                          </span>
                        </td>
                        <td className="px-4 py-3 text-sm text-slate-700">#{assignment.matched_order_id}</td>
                        <td className="px-4 py-3 text-xs text-slate-600">{formatDateTime(assignment.assigned_at)}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            )}

            {assignments.length > 0 && (
              <div className="mt-4 pt-4 border-t border-slate-200 flex justify-between items-center bg-slate-50 p-3 rounded">
                <span className="text-sm text-slate-600">Total assignments: <span className="font-bold text-slate-900">{assignments.length}</span></span>
                <span className="text-sm font-bold text-slate-900">
                  Total Assigned: <span className="text-blue-600">{assignments.reduce((sum, a) => sum + a.assigned_qty, 0)}</span> units
                </span>
              </div>
            )}
          </div>

          {/* Timestamps Footer */}
          <div className="bg-slate-50 rounded-lg border border-slate-200 p-4 text-xs text-slate-600 space-y-2">
            <p><span className="font-semibold text-slate-700">Created:</span> {formatDateTime(buyerHistory.created_at)}</p>
            <p><span className="font-semibold text-slate-700">Last Updated:</span> {formatDateTime(buyerHistory.updated_at)}</p>
          </div>
        </div>
      </div>
    </div>
  );
}